#wordText {
    grid-column: 1/-1;
    grid-row: 2/3;
    padding-top: 5px;
    border-top: 1px solid black;
}

#radio1 {
    grid-column: 1/-1;
    grid-row: 3/4;
}

#radio2 {
    grid-column: 1/-1;
    grid-row: 4/5;
}

#radio3 {
    grid-column: 1/-1;
    grid-row: 5/6;
}

/* this is part of App */
.rightDiv {
    grid-column: 2/3;
    grid-row: 4/5;
    background-color: white;
    border-radius: 10px;
    padding: 10px;
}

.wrongDiv {
    grid-column: 2/3;
    grid-row: 4/5;
    background-color: white;
    border-radius: 10px;
    padding: 10px;
}

.mwLogoDiv {
    grid-column: 1/-1;
    grid-row: 6/7;
    background-color: white;
    align-self: center;
    padding-bottom: 20px;
}

#mwLogo {
    height: 50px;
}


@media (max-width: 1024px) {
    button {
        border: 0.5px solid black;
        border-radius: 5px;
        background-color: transparent;
        margin: 5px;
    }
}


